body {
  background: rgb(191, 250, 250);
}
td {
  border: 1px solid black;
  padding: 5px;
}
td:hover {
  background: rgb(250, 232, 232);
  cursor: pointer;
}
.sort:hover {
  background: rgb(179, 126, 248);
  cursor: pointer;
}
.Textarea {
  background: rgb(87, 255, 255);
  width: 95%;
  height: 65px;
  margin-right: 15px;
  border: none;
}
.Focus {
  background: rgb(68, 243, 97);
}
.Content {
  margin: 30px 80px;
  
}
table {
  border-collapse: collapse;
  margin-top: 10px;
  margin-bottom: 10px;
}
.Search {
  float: right;
}
button {
  margin-left: 5px;
}
.Datatable {
  background:rgb(98, 100, 100);
  text-align: center;
  padding: 10px;
  border-radius: 10px 10px 0 0;
}